@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
    /* font */
    --logo-font:  'Titillium Web', sans-serif;
    --box-title-font:'Jua', sans-serif;
    --menu-font:'Jua', sans-serif;
    --list-title-font:'Jua', sans-serif;
    --list-item-font:'Jua', sans-serif;
    --normal-font:'Gothic A1', sans-serif;

    /* color */
    --theme-color:#2E3E58;
    --theme-color-light:#596885;
    --main-background-color:grey;
}

*{
    box-sizing: border-box;
}

body{
    margin:0;
}
p{
    margin: 0;
  white-space: nowrap;
  font-family: 'Gothic A1', sans-serif;
  font-family: var(--normal-font);
  font-weight: 700;

}
a{
    text-decoration: none;
    color: black;
}
ul,li{
    list-style: none;
}
.navbar{
    padding:30px;
}
.container{
    
}




.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* .navbar-menu.container{
    width: 1160px;
} */
.navbar-menu{
    display: grid;
    /* grid-template-areas: "logo menu-list search"; */
    grid-template-columns: 230px minmax(550px,2fr) minmax(400px,1.5fr);
    align-items: center;
    background-color: #596885;
}

.logo-img{
    width: 80px;
}
.navbar-menu .logo{
    display: flex;
    align-items: center;
    font-size: 30px;
    font-family: 'Bebas Neue', cursive;
    background-color: var(--theme-color);
    padding: 10px;
    box-shadow: 1px -1px 5px black;
    
}
.logo img{
    margin-left: 20px;
}
.logo h1{
    margin-top: 10px;
    font-size: 30px;
    color: white;
    font-family: var(--logo-font);
    font-weight: 550;
}
.menu-list{
    justify-self: center;
}
.menu-items{
    display: flex;
    justify-content: space-around;
    margin:0;
    min-width: 600px;
    font-size: 24px;
    font-family: var(--menu-font);
    /* font-family: 'Do Hyeon', sans-serif; */
    color:white;
}

.search{
    justify-content: center;
    display: flex;
    
}

.searchInput{
    width: 70%;
    min-width: 10px;

    padding: 0;
    border: none;
    border-radius: 4px;
    border-top-right-radius: 0;border-bottom-right-radius: 0;
    line-height: 38px;
}
.search button{
    color: white;
    border: 1px solid white;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;

}
@media screen and (max-width:1320px) {
    .search{
        min-width: 300px;
        float: right;
    }
    
}
.news-item{
    width: 700px;
    border: 1px solid;
    padding: 20px;
    border-radius: 5px;
    margin: 5px;
}

.news-item .subInfo{
    text-align: left;
}
.subInfo .provider{
    font-size: 20px;
}

.news-item .content{
    display: -webkit-box;
    word-wrap: break-word;
    height: 80px;
    width: 100%;
    -webkit-line-clamp: 3;
    -webkit-box-orient:vertical ;
    text-align: left;
    
    margin: 0;
    overflow-y: hidden;
    text-overflow: ellipsis;

    line-height: 24px;
    height: 72px;
}
.news-item .news-title{
    display: block;
    margin-top: 10px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: left;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space:nowrap;
}
/* Grid css */
.main{
    display: grid;
    grid-auto-rows: repeat(3, auto);
    grid-template-columns: 230px minmax(475px,1fr) minmax(475px,1fr);
    background-color: #ECECEC;
    padding-right: 30px;
    grid-gap: 30px;
    gap: 30px;
}
.box-item{
    box-shadow: 5px 5px 5px  gray;
    align-self:start;
    background-color: white;
    border-radius: 4px;
    color: var(--theme-color);
}
.box-title{
    background-color: var(--theme-color);
    border-top-left-radius: 4px;border-top-right-radius: 4px;  
    padding-top:10px;padding-bottom: 10px;
}
.box-item h5{
    font-family: var(--box-title-font);
    font-size: 24px;
    color: white;
    margin: 0;

}

/* 후보자 리스트 */

  .candidates{
    grid-row: 1/4;
    grid-column: 1/2;
    box-sizing: border-box;
    padding: 20px;
    box-shadow: 1px 1px 5px black;
    background-color: white;
    
  }
  .candidates .title{
      font-family: var(--list-title-font);
      font-size: 24px;
      border-bottom: 1px solid;
      padding: 20px;
  }
  /* 후보자 정보 */
  .candidate{
    display: flex;
    grid-row: 1/2;
    grid-column: 2/3;
    padding:30px;
    margin-top: 50px;
  }
  .candidate h5{
      background-color: transparent;
      color: black;
  }
  /* 공약 리스트 */
  .promiseList{
    align-self: flex-start;
    grid-row: 3/4;
    grid-column: 2/3;
  }
  /* 연관뉴스 */
  .news{
    grid-row: 2/3;
    grid-column: 2/3;
  
  }
  /* 키워드 클라우드 */
  .wordCloud{
    margin-top: 50px;
    grid-row: 1/2;
    grid-column: 3/4;
  }
  .age{
    grid-row: 2/3;
    grid-column: 3/4;
  }
  
  
  .candidateInfo-enter{
    opacity: 0;
  }
  .candidateInfo-enter-active{
    opacity: 1;
    transition: all 300ms;
  }
  .nav-item{
    border-bottom: 1px solid;
    color: black;
    padding-top: 10px;
    padding-bottom: 10px;
    font-family: var(--list-item-font);
    font-size:16px;
  }
  
  /* 후보자 관련 글 */
  

  .candidate img{
    height:170px;
    width: auto;
  }
  .candidate .info{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 20px;
  }
  
  .promiseList .title{
    width: 100%;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    
  
  }
  .title h5{
      margin:auto;
  }
  .promisePossiblity{
    display: flex;
    align-items: center;
    height: 25px;
    padding-right: 10px;
    color: var(--theme-color-light);
    font-family: var(--menu-font);

    color: white;
  }
  
  .promise-title{
    display: flex;
    justify-content: space-between;
    padding: 7px;
  
    overflow: hidden;
    text-overflow: ellipsis;    
    text-align: left;
    font-family: var(--menu-font);
  }
  .possiblity{
    margin-left: 5px;
    width:50px;
    text-align: center;
    height: 25px;
    border-radius: 4px;
    color: var(--theme-color);
  }
  
  .promiseList button{
    border: none;
    background-color: white;
    color:var(--theme-color);
    font-weight: 600;
  }
  
  
 
  /* p 태그 넘어가는 부분 overflow */
  .news p{
    display: block;
    padding: 7px;
    overflow: hidden;
    text-overflow: ellipsis;    
    text-align: left;
  }
  .news button{
    border: none;
    background-color: white;
    color:var(--theme-color);
    font-weight: 600;
  }
  
  /* 후보자 관련 시각자료 */
  
  
  .wordCloud img{
    width: 100%;
    
  }
  
  .age img{
    width: 100%;
  }

